import React from 'react';
import './SaleSlider.css';
import Slider from 'react-slick';
import bekon from '../../images/SaleSlider/bekon.jpg';
import chease from '../../images/SaleSlider/chease.jpg';
import erik from '../../images/SaleSlider/erik.jpg';
import grib from '../../images/SaleSlider/grib.jpg';
import hrust from '../../images/SaleSlider/hrust.jpg';
import pite from '../../images/SaleSlider/pite.jpg';
import shaurmalay from '../../images/SaleSlider/shaurmalay.jpg';
import sitnik from '../../images/SaleSlider/sitnik.jpg';
const SaleSlider = () => {
    const settings = {
        arrows: false,
        infinite: true,
        centerMode: true,
        centerPadding: '100px',
        slidesToShow: 2,
        slidesToScroll: 1,
        speed: 2000,
        autoplay: true,
        autoplaySpeed: 2000,
        initialSlide: 0,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 2,
                    centerPadding: '40px',
                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    centerPadding: '20px',
                }
            }
        ],
    };
    return (
        <div className='container mb-5'>
            <div className='row'>
                <div className='col-12'>
                    <Slider {...settings} className="sale-slider-container">
                        <img src={bekon} alt="" />
                        <img src={chease} alt="" />
                        <img src={erik} alt="" />
                        <img src={grib} alt="" />
                        <img src={hrust} alt="" />
                        <img src={pite} alt="" />
                        <img src={shaurmalay} alt="" />
                        <img src={sitnik} alt="" />
                    </Slider>
                </div>
            </div>
        </div>
    )
}








export default SaleSlider;