import React from 'react'
import './Tizers.css';
import time from '../../images/Tizers/time.svg';
import price from '../../images/Tizers/price.svg';
import rocket from '../../images/Tizers/rocket.svg';
const Tizers = () => {
    return (
        <div className='container mb-5'>
            <div className='row'>
                <div className='col-12'>
                    <div className='tizers-container'>
                        <div className='tizer-item'>
                            <img src={time} alt=" " />
                            <div className='tizer-item__description'>
                                <span className='item__description-title'> 60 .</span>
                                <span className='item__description-desc'> </span>
                            </div>
                        </div>

                        <div className='tizer-item'>
                            <img src={price} alt=" " />
                            <div className='tizer-item__description'>
                                <span className='item__description-title'>200</span>
                                <span className='item__description-desc'>.  </span>
                            </div>
                        </div>

                        <div className='tizer-item'>
                            <img src={rocket} alt=" " />
                            <div className='tizer-item__description'>
                                <span className='item__description-title'> 500 . </span>
                                <span className='item__description-desc'> </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Tizers;